table {
    border-collapse: collapse;
    width: 100%;
  }
  
  th, td {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  th {
    background-color: #f2f2f2; /* Header row background color */
  }
  
  /* Optional: Hover effect */
  tr:hover {
    background-color: #f2f2f2;
  }
  